import React from 'react'

const BannerCountry = props => (
  <section
    id="banner"
    className="style2"
    style={{
      backgroundImage:
        'url(' + (props.bgImage ? props.bgImage.src : null) + ')',
    }}
  >
    {/* <BackgroundImage
  //   id="banner"
  //   className="style2"
  //   Tag="section"
  //   // className={className}
  //   fluid={props.bgImage}
  //   // backgroundColor={`#040e18`}
  >*/}
    <div className="inner">
      <header className="major">
        <h1>{props.title}</h1>
      </header>
      <div className="content">
        <p>
          Should I Tip In {props.title}
        </p>
      </div>
    </div>
    {/* </BackgroundImage> */}
  </section>
)

export default BannerCountry
