export const tippingContentQuery = graphql`
  query TippingContentByPath($path: String!, $image: String!) {
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
      frontmatter {
        path
        title
        author
        date
        image
      }
    }
    image: file(relativePath: { eq: $image }) {
      childImageSharp {
        fluid(maxWidth: 1080) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`
